@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Montserrat:wght@400;500&family=Norican&family=Poppins:wght@400;500&display=swap);
.allProjects_container{background-color:#f5f5f5;padding:100px 10px 50px 10px}.allProjects_container .allProjects_title{margin-bottom:2rem;font-size:2rem;font-family:"Montserrat";text-align:center}
.projects_container{max-width:1200px;width:100%;margin:0 auto;padding:80px 20px}.projects_container .projects_title{font-family:"Montserrat";font-size:1.3rem;margin-bottom:3rem;font-weight:500;position:relative}.projects_container .projects_title::before{content:"";width:50px;height:4px;background:#3d5af1;position:absolute;bottom:-10px;border-radius:50px}.projects_container .more_projects{text-decoration:none;color:#fff;background:#0b68de;padding:10px 55px;font-size:1.1rem;transition:all .2s;cursor:pointer;display:flex;border:none;align-items:center;margin:3rem auto 0 auto;display:block}.project_card{display:flex;grid-gap:2rem;gap:2rem;background:#fff;justify-content:center;max-width:850px;margin:0 auto 2rem auto;padding:2rem 2rem;box-shadow:4px 4px 18px rgba(0,0,0,.15)}@media(max-width: 780px){.project_card{flex-direction:column}}.project_card .project_details{flex:1 1}.project_card .project_details .project_desc{margin-top:.5rem;line-height:1.3rem}.project_card .project_details .project_interect{margin-top:2rem;display:flex;align-items:center;flex-wrap:wrap;grid-gap:1rem;gap:1rem;justify-content:space-between}.project_card .project_details .project_interect .case_study{background:#0e153a;color:#fff;border:none;padding:10px 20px;font-size:.8rem;font-family:"Montserrat";font-weight:500;display:flex;cursor:pointer}.project_card .project_details .project_interect .case_study span{margin-left:.5rem}.project_card .project_details .project_interect .project_links .project_live,.project_card .project_details .project_interect .project_links .project_github{font-weight:bold;font-family:"Montserrat";cursor:pointer;color:#000}.project_card .project_details .project_interect .project_links .project_live{margin-right:1rem}.project_card .project_img{max-width:450px;width:100%;height:220px;border:2px solid #3d5af1}@media(max-width: 600px){.project_card .project_img{height:auto;border:none}.project_card .project_img>img{border:2px solid #3d5af1}}.project_card .project_img img{width:100%;height:100%}
